.clear {
    height: 80px;
    font-size: 1.6em;
    display: flex;
    flex: 1;
    background-color: #ac0246;
    margin-top: 8px;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    color: white;
    border: 2px solid white;
    cursor: pointer;

    border-radius: 50px;
}

.clear:hover {
    box-shadow: inset -2px 2px 15px rgb(246, 128, 193);
}