.App {
    height: 100vh;
    padding-top: 10px;
    background-color: rgb(9, 10, 35);
    font-family: Lato, sans-serif;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.freecodecamp-logo-contenedor {
    width: 100%;
    height: 50px;
    margin: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.freecodecamp-logo {
    height: 40px;
}

.contenedor-calculadora {
    width: 400px;
    height: 590px;
    padding: 15px;
    background-color: #99c9ff;
    border: 5px solid white;
    border-radius: 20px;
}

.fila {
    margin: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
}