.boton-contenedor {
  height: 83px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 38px;
  background-color: rgb(9, 10, 35);
  color: white;
  border-radius: 25px;
  border: 2px solid white;
  margin: 4px;
  cursor: pointer;
  user-select: none;
  flex: 1 1;
}

.operador {
  background-color: #00471b;
}

.boton-contenedor:hover {
  box-shadow: inset -2px 2px 10px rgb(255, 255, 255);
}

.operador:hover {
  box-shadow: inset -2px 2px 10px rgb(161, 221, 167);
}