.input {
  height: 90px;
  border-radius: 50px;
  margin-bottom: 20px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-weight: bold;
  overflow-x: auto;
  font-size: 40px;
  background-color: rgb(9, 10, 35);
  color: white;
  border: 2px solid #888;
  padding: 0 30px;
  letter-spacing: 8px;
  box-shadow: 1px 2px 1px 1px white;
}